@import "../../app.scss";

.sidebar{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgb(207, 121, 95);
    color: lightgray;
   

    .bg{
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        width: 400px;
        background: rgb(207, 121, 95);
        z-index: 999;

        @include mobile {
            width: 200px;
            height: 100%;
        }

        .links{
            position: absolute;
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            gap: 20px;

            a{
                font-size: 40px;
                
                @include mobile {
                  font-size: 20px;
                }
            }

        }
    }

    button{
        width: 50px;
        height: 50px;
        border-radius: 50%;
        position: fixed;
        top: 25px;
        left: 25px;
        background: transparent;
        border: none;
        z-index: 999;
    }
}