@import "../../app.scss";

.navbar{
    height: 100px;

    .wrapper{
        max-width: 1200px;
        margin: auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 100%;

        @include mobile{
           justify-content: flex-end;
           padding: 20px;
        }

        span{
            font-weight: bold;
            @include mobile{
                display: none;
            }
        }

        .social{
            display: flex;
            gap: 20px;

            img{
                height: 18px;
                width: 18px;
            }
        }
    }
}